<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "classQualificationManagementComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      dateRange: [],
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "courseInfo",
          label: "課程名稱(課表編號)",
          width: "300",
        },
        {
          id: "class_Date",
          label: "上課日期",
          width: "150",
        },
        {
          id: "status_Id",
          label: "課表狀態",
          width: "100",
        },
        {
          id: "register_Count",
          label: "已報名人數",
          width: "100",
        },
      ],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    async changeDaterange() {
      try {
        if (!this.dateRange || this.dateRange === null) return;
        const day1 = this.dateRange[0];
        const day2 = this.dateRange[1];
        const res = await this.$store.dispatch("qryQualifyList", {
          day1,
          day2,
        });
        const data = res.data.data;
        this.tableData = JSON.parse(JSON.stringify(data));
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.courseInfo = `${e.course_Name}(${e.schedule_No})`;
        });
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    changePageHandler(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
  },
  async mounted() {
    this.$root.overlay = false;
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./classQualificationManagement.html"></template>
